.list {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

a {
    text-decoration: none;
    transition: all 0.3s ease-in-out;

    &:hover,
    &:focus {
        text-decoration: none;
        outline: none;
    }
}

.row.m0 {
    margin: 0px;
}

button:focus {
    outline: none;
    box-shadow: none;
}

.p_120 {
    padding-top: 120px;
    padding-bottom: 120px;
}

.pad_top {
    padding-top: 120px;
}

.mt-25 {
    margin-top: 25px;
}

.container {
    @media(min-width:1200px) {
        max-width: 1170px;
    }
}

img {
    max-width: 100%;
}

// ul {
//     list-style: none;
//     padding: 0;
// }

@media(min-width: 1620px) {
    .box_1620 {
        max-width: 1650px;
        margin: auto;
    }
}


/* Main Title Area css
============================================================================================ */
.main_title {
    text-align: center;
    max-width: 680px;
    margin: 0px auto 75px;

    h2 {
        font-family: $rob;
        font-size: 36px;
        color: $dip;
        margin-bottom: 12px;
    }

    p {
        font-size: 14px;
        font-family: $rob;
        font-weight: normal;
        line-height: 24px;
        color: $pfont;
        margin-bottom: 0px;
    }
}

/* End Main Title Area css
============================================================================================ */